@import "../../inc/vars";
.acro-innovations-block {
	padding: 80px 0;
	&_title {
		text-align: center;
		width: 100%;
		margin-bottom: 40px;
		padding: 0 30px;
		@media(min-width: $screen-sm) {
			width: 60%;
			margin: 0 auto 60px auto;
		}
		h2 {
			@include pop(400, 35, 1.24);
			background: var(--grad-title, linear-gradient(180deg, #2D3442 0%, #6C7689 100%));
			background-clip: text;
			-webkit-background-clip: text;
			-webkit-text-fill-color: transparent;
			margin-bottom: 16px;
			@media(min-width: $screen-sm) {
				margin-bottom: 8px;
				@include pop(400, 53, 1.24);
			}
		}
		.description {
			@include fig(400, 20, 1.56);
			color: #4F4B58;
		}
	}
	.acro-innovations-block_items_wrapper {
		overflow-x: auto;
		position: relative;
		display: block;
		justify-content: center;
		padding-top: 20px;
		@media(min-width: $screen-md) {
			display: flex;
			flex-wrap: wrap;
			overflow-x: unset;
		}
		.circles {
			width: 805px;
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			@media(min-width: $screen-md) {
				margin-left: auto;
				margin-right: auto;
			}
			.circle1,
			.circle2,
			.circle3,
			.circle4 {
				position: absolute;
				//z-index: -1;
				width: 90%;
				padding-bottom: 90%;
				border-radius: 50%;
				left: 50%;
				top: 50%;
				transform: translate(-50%, -50%);
				animation: pulseCircle 3s infinite;
			}
			.circle1 {
				border: 5px solid var(--purple-purple-50, #F1EDFA);
				opacity: 0.4;
			}
			.circle2 {
				border: 6px solid var(--purple-purple-300, #A991DD);
				opacity: 0.1;
				width: 80%;
				padding-bottom: 80%;
				animation-delay: 0.5s;
			}
			.circle3 {
				border: 8px solid var(--purple-purple-200, #C6B6E9);
				opacity: 0.1;
				width: 70%;
				padding-bottom: 70%;
				animation-delay: 1s;
			}
			.circle4 {
				opacity: 0.1;
				background: linear-gradient(141deg, #7048C7 15.28%, #E2DAF4 64.76%);
				width: 60%;
				padding-bottom: 60%;
				animation-delay: 1.5s;
			}
		}

	}
	&_items {
		width: 1000px;
		padding: 60px 20px;
		display: flex;
		flex-wrap: wrap;
		@media(min-width: $screen-md) {
			margin-left: auto;
			margin-right: auto;
		}
		.list-item {
			width: 33.3%;
			padding: 0 40px;
			position: relative;
			margin-bottom: 40px;
			&:before {
				content: "";
				display: block;
				width: 80%;
				height: 80%;
				border-radius: 100%;
				background: rgba(102, 37, 133, 0.3);
				filter: blur(20px);
				position: absolute;
				left: 50%;
				bottom: -30px;
				transform: translateX(-50%);
				z-index: 3;
			}
			.years {
				width: 100%;
				display: flex;
				align-items: flex-end;
				text-align: center;
				.logo {
					transition: .4s;
					position: relative;
					z-index: 4;
				}
				.year {
					color: #fff;
					@include pop(600, 20, 1.24);
					position: relative;
					transition: .4s;
					z-index: 4;
					.year-value {
						position: relative;
						z-index: 4;
					}
					&:before {
						content: '';
						display: block;
						width: 100%;
						height: 17px;
						background: #A991DD;
						left: 0;
						bottom: 100%;
						position: absolute;
					}
					&:after {
						content: '';
						display: block;
						width: 100%;
						height: 100%;
						background: rgb(112,72,199);
						background: linear-gradient(180deg, rgba(112,72,199,1) 0%, rgba(169,145,221,1) 100%);
						left: 0;
						top: 0;
						position: absolute;
						z-index: 2;
					}
					div {
						position: relative;
						z-index: 1;
					}
				}
				.logo {
					margin-bottom: 28px;
					@include pop(600, 12, 1.24);
					padding: 0 10px;
				}
				.counterpath {
					width: 30%;
					.year {
						padding: 25px 8px 25px;
						margin-right: -3px;
						&:before {
							clip-path: polygon(20% 0, 97% 0, 100% 100%, 0% 100%);
						}
						.year-value {
							background: var(--White-Heading, linear-gradient(180deg, rgba(255, 255, 255, 0.25) 0%, #FFF 0.01%, rgba(255, 255, 255, 0.60) 100%));
							background-clip: text;
							-webkit-background-clip: text;
							-webkit-text-fill-color: transparent;
						}
					}
				}
				.acro {
					width: 40%;
					position: relative;
					z-index: 5;
					.year {
						padding: 47px 8px 32px;
						@include pop(600, 30, 1.24);
						z-index: 2;
						.year-subtitle {
							@include pop(400, 11, 1);
							text-align: center;
							color: #fff;
							z-index: 3;
							position: absolute;
							left: 5px;
							right: 5px;
							width: auto;
							bottom: 23px;
						}
						&:before {
							clip-path: polygon(15% 0%, 85% 0%, 100% 100%, 0% 100%);
							z-index: 2;
						}
						.hidden-wrapper {
							position: absolute;
							transition: .5s;
							left: 50%;
							top: 0;
							transform: translateX(-50%);
							opacity: 0;
							z-index: 1;
							.trophy {
								position: absolute;
								transform: rotate(27deg);
								left: -95px;
								top: -37px;
								transition: .5s;
								transform-origin: bottom;
							}
							.confetti {
								position: absolute;
								transition: .7s;
								left: 50%;
								top: 0;
								transform: translateX(-50%);
								svg {
									width: 0;
									transition: .7s
								}
							}
							svg {
								width: auto;
							}
						}
					}
					.logo {
						position: relative;
						padding-bottom: 25px;
						.branches {
							position: absolute;
							left: 50%;
							bottom: 0;
							transform: translateX(-50%);
						}
					}
				}
				.opensource {
					width: 30%;
					.year {
						padding: 5px 8px 18px;
						margin-left: -3px;
						&:before {
							clip-path: polygon(3% 0, 80% 0, 100% 100%, 0% 100%);
						}
						.year-value {
							background: var(--White-Heading, linear-gradient(180deg, rgba(255, 255, 255, 0.25) 0%, #FFF 0.01%, rgba(255, 255, 255, 0.60) 100%));
							background-clip: text;
							-webkit-background-clip: text;
							-webkit-text-fill-color: transparent;
						}
					}
				}
			}
			.listContent {
				position: relative;
				padding: 8px 6px;
				border-radius: 20px;
				background: #fff;
				margin-top: -15px;
				z-index: 6;
				img {
					width: 100%;
					cursor: pointer;
				}
				.list-item__feature {
					padding: 7px 0 12px;
					text-align: center;
					@include pop(400, 15, 1.56);
				}
			}
			.list-item__link {
				position: absolute;
				left: 40px;
				bottom: 0;
				right: 40px;
				width: auto;
				overflow: hidden;
				background: #fff;
				border-radius: 0 0 20px 20px;
				padding: 8px 16px;
				display: flex;
				align-items: center;
				text-overflow: clip;
				z-index: 7;
				img {
					margin-right: 10px;
					width: 20px;
					cursor: pointer;
				}
				a {
					display: block;
					white-space: nowrap;
					max-width: calc(100% - 30px);
					text-overflow: ellipsis;
					overflow: hidden;
					width: 160px;
					height: 1.2em;
					@include pop(600, 12, 1.24);
				}
			}

			&:hover {
				.years {
					.counterpath {
						.year, .logo {
							transform: rotate(-8deg);
						}
					}
					.acro {
						.year {
							transform: translateY(-10px);
							.hidden-wrapper {
								opacity: 1;
								.trophy {
									transform: translate(-10px, -70px) rotate(0deg);
								}
								.confetti {
									top: -100px;
									svg {
										width: 216px;
									}
								}
							}
						}
					}
					.opensource {
						.year, .logo {
							transform: rotate(8deg);
						}
					}
				}
			}
		}
	}
}

@keyframes pulseCircle {
	0% {
		transform: translate(-50%, -50%) scale(1);
	}
	50% {
		transform: translate(-50%, -50%) scale(1.1);
	}
	100% {
		transform: translate(-50%, -50%) scale(1);
	}
}